<template>
  <div class="container-fluid">
    <div class="row">
      <div class="alert alertStyle alert-primary hide" data-dismiss="alert" role="alert">
        <span aria-hidden="true">The PIN number you have entered is not valid. Please try again!</span>
      </div>
      <div class="col-md-12">
        <h1 class="display-3">TPO Dispatch</h1>
      </div>
      <div class="card col-md-3" v-for="(req, index) in reqs" :key="index">
        <div class="card-body">
          <h5 class="card-title">Stock #: {{req.stock_number}}</h5>
          <p class="card-text">Sales Associate: {{req.sales_associate}}</p>
          <p class="card-text">Deal #: {{req.deal_number}}</p>
          <p class="card-text">Customer Name: {{req.customer_name}}</p>
          <p class="card-text">Year: {{req.year}}</p>
          <p class="card-text">Model: {{req.model}}</p>
          <p v-if="req.customer_care_package == 'Yes'" class="card-text badge yesccp">Customer Care Package: {{req.customer_care_package}}</p>
          <p v-else-if="req.customer_care_package == 'No'" class="card-text badge noccp">Customer Care Package: {{req.customer_care_package}}</p>
          <p v-if="req.accessories == 'Yes'" class="card-text badge yesccp">Accessories: {{req.accessories}}</p>
          <p v-else-if="req.accessories == 'No'" class="card-text badge noccp">Accessories: {{req.accessories}}</p>
          <p class="card-text" v-if="req.accessories == 'Yes'">List Accessories: {{req.list_accessories}}</p>
          <hr />
          <form @submit.prevent="updateSalesReq(req.completionSales, req.salesManager, req.id, req.password)">
            <label for="completionSales">Approval Status</label>
            <select
              id="completionSales"
              class="form-control form-control-lg"
              v-model="req.completionSales"
              required
            >
              <option v-for="(approval, index) in approvals" :key="index">{{ approval }}</option>
            </select>
            <hr />
            <label for="salesManager">Manager</label>
            <select
              id="salesManager"
              class="form-control form-control-lg"
              v-model="req.salesManager"
              required
            >
              <option v-for="(man, index) in managers" :key="index">{{ man }}</option>
            </select>
            <hr />
            <label for="salesManager">PIN</label>
            <input class="form-control form-control-lg" type="password" v-model="req.password" />
            <v-btn color="success" type="submit">Submit</v-btn>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { db } from "../main";
import router from "../router";

export default {
  name: "Preowned",
  data() {
    return {
      reqs: [],
      approvals: ["Approved", "Denied"],
      managers: [
        "Barry Laird",
        "Rolando Rocha",
        "Miguel Valdez",
        "Yvone Garcia",
        "Don Wilkinson",
        "Joshua Barazza",
        "Bryan Barton"
      ],
      completionSales: "",
      salesManager: ""
    };
  },
  firestore() {
    return {
      reqs: db
        .collection("makeReady")
        .where("type_of_vehicle", "==", "Pre-owned")
        .where("completionSales", "==", "Incomplete")
    };
  },
  methods: {
    updateSalesReq(status, manager, id, password) {
      if (status !== "" && manager !== "") {
        if (
          (manager === "Barry Laird" && password === "2419") ||
          (manager === "Rolando Rocha" && password === "7206") ||
          (manager === 'Don Wilkinson' && password === '5563') ||
          (manager === 'Joshua Barazza' && password === '7030') ||
          (manager === "Miguel Valdez" && password === '8790') ||
          (manager === 'Yvone Garcia' && password === '4598') ||
          (manager === "Bryan Barton" && password === "6969")
        ) {
          console.log("it worked");
          if (status === "Approved") {
            db.collection("makeReady")
              .doc(id)
              .update({
                completionSales: "Approved",
                salesManager: manager,
                completionFinance: "Incomplete",
                completionCarWash: "Incomplete",
                partsClaimedStatus: "Incomplete",
                partsclaimed: true,
                salesTimestamp: new Date()
              });
          } else {
            db.collection("makeReady")
              .doc(id)
              .update({
                completionSales: "Denied",
                salesManager: manager,
                salesTimestamp: new Date()
              });
          }
        } else {
            $('.hide').alert().removeClass('hide')
            
        }
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
    margin-top: 20px;
    margin-bottom: 30px;
}

button {
    margin-top: 15px;
}

.btn-primary {
  background-color: #0096a5;
  border-color: #0096a5;
}

.container-fluid {
    width: 90%;
}

.alertStyle {
  position: fixed; /* Sit on top of the page content */
  width: 100%; /* Full width (cover the whole page) */
  height: 10%; /* Full height (cover the whole page) */
  padding-top: 40px;
  margin: auto 0;
  top: 0; 
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2; /* Specify a stack order in case you're using a different order for other elements */
  cursor: pointer; /* Add a pointer on hover */
  vertical-align: middle;
}

.hide {
  display: none;
}
</style>



